.card-item {
  display: block;
  max-width: 570px;
  outline: 1px solid $card-border;
  border-left: 3px solid $main-blue;
  min-height: 299px;
  padding: 40px 43px 49px;

  @media (max-width: $mobile-width-only) {
    max-width: 290px;
    min-height: 232px;
    padding: 25px 13px 20px 25px;
  }
}

.card-item h3 {
  @include font-family-condensed;
  font-weight: normal;
  color: $main-blue;
  font-size: 24px;
  line-height: 140%;
  margin: 0 0 15px;

  @media (max-width: $mobile-width-only) {
    font-size: 20px;
  }
}

.card-item p {
  font-size: 16px;
  line-height: 135%;
  color: $text-grey-color;
  margin: 0;
}

.card-item__icon {
  transition: 2s;
  fill: $main-blue;
}

.card-item:hover {
  transition: 2s;
  background-color: $main-blue;
}

.card-item:hover h3,
.card-item:hover p {
  transition: 2s;
  color: $ordinary-white;
}

.card-item:hover .card-item__icon {
  fill: $ordinary-white;
}
